        'Â§ßÂêâ': 'ÊÇ®ÁöÑÊï¥È´îÈÅãÂã¢ÈùûÂ∏∏Âº∑ÂãÅÔºåÂêÑÊñπÈù¢ÈÉΩÊúâËâØÂ•ΩÁöÑÁôºÂ±ïÊ©üÊúÉ„ÄÇÈÄôÊòØÂØ¶ÁèæÁõÆÊ®ôÁöÑÁµï‰Ω≥ÊôÇÊ©ü„ÄÇ',
        'Âêâ': 'ÊÇ®ÁöÑÈÅãÂã¢Á©©ÂÆöÂêë‰∏äÔºåÈõñÁÑ∂ÂèØËÉΩÊúÉÊúâ‰∏Ä‰∫õÂ∞èÊåëÊà∞Ôºå‰ΩÜÊï¥È´îË∂®Âã¢ÊòØÁ©çÊ•µÁöÑ„ÄÇ',
        '‰∏≠': 'ÊÇ®ÁöÑÈÅãÂã¢Âπ≥Á©©ÔºåÈÅ©ÂêàÁ©©Á¥ÆÁ©©ÊâìÔºå‰∏çË¶ÅÊÄ•ÊñºÊ±ÇÊàêÔºåÈáçÈªûÊòØ‰øùÊåÅÁèæÊúâÁöÑÂÑ™Âã¢„ÄÇ',
        'Âá∂': 'ÊÇ®ÁöÑÈÅãÂã¢Èù¢Ëá®‰∏Ä‰∫õÊåëÊà∞ÔºåÈúÄË¶ÅË¨πÊÖéË°å‰∫ãÔºå‰ΩÜÈÄô‰πüÊòØÊàêÈï∑ÂíåÂ≠∏ÁøíÁöÑÊ©üÊúÉ„ÄÇ'
      };
      
      return descriptions[overview] || 'ÊÇ®ÁöÑÈÅãÂã¢ÊúâÂÖ∂Áç®ÁâπÁöÑÁôºÂ±ïËªåË∑°„ÄÇ';
    },
    
    async goToTransitAnalysis() {
      if (!this.calculationResults) {
        alert('Ë´ãÂÖàÂÆåÊàêÂç†ÊòüÂàÜÊûê');
        return;
      }
      
      try {
        await this.$router.push({ name: 'transit-analysis' });
      } catch (error) {
        console.error('È†ÅÈù¢Ë∑≥ËΩâÂ§±Êïó:', error);
        alert('È†ÅÈù¢Ë∑≥ËΩâÂ§±ÊïóÔºåË´ãÁ®çÂæåÈáçË©¶');
      }
    },
    
    openCompatibilityTool() {
      alert('ÂêàÁõ§ÂàÜÊûêÂäüËÉΩÂç≥Â∞áÊé®Âá∫ÔºåÊï¨Ë´ãÊúüÂæÖÔºÅ');
    },
    
    shareResults() {
      if (navigator.share) {
        navigator.share({
          title: 'Âç†ÊòüÂàÜÊûêÁµêÊûú',
          text: `${this.userData.name}ÁöÑÂç†ÊòüÂàÜÊûêÁµêÊûú`,
          url: window.location.href
        }).catch(console.error);
      } else {
        navigator.clipboard.writeText(window.location.href)
          .then(() => alert('URL„Çí„ÇØ„É™„ÉÉ„Éó„Éú„Éº„Éâ„Å´„Ç≥„Éî„Éº„Åó„Åæ„Åó„Åü'))
          .catch(() => alert('ÊâãÂãï„ÅßURL„Çí„Ç≥„Éî„Éº„Åó„Å¶„Åè„Å†„Åï„ÅÑ'));
      }
    },
    
    saveResults() {
      const data = {
        userData: this.userData,
        results: this.calculationResults,
        timestamp: new Date().toISOString()
      };
      
      const blob = new Blob([JSON.stringify(data, null, 2)], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `astrology-results-${this.userData.name}-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
    
    goBack() {
      this.$router.push({ name: 'home' });
    },
    
    updateChartSize() {
      const container = this.$el?.querySelector('.chart-container');
      if (container) {
        const containerWidth = container.clientWidth;
        this.chartSize = Math.min(containerWidth - 40, 600);
      }
    }
  },
  
  created() {
    if (!this.userData || !this.calculationResults) {
      this.$router.push({ name: 'home' });
    }
  },
  
  mounted() {
    this.updateChartSize();
    window.addEventListener('resize', this.updateChartSize);
  },
  
  beforeUnmount() {
    window.removeEventListener('resize', this.updateChartSize);
  }
};
</script>

